interface Params {
  name: string;
  age: number;
}

function mockFetch(params: Params): Promise<{ data: Params }> {
  return new Promise((resolve) => {
    setTimeout(() => {
      resolve({ data: params });
    }, 1000);
  });
}

(async () => {
  const data = await mockFetch({ name: 'yuzhicheng', age: 18 });
  console.log(data);
})();

const enum Item {
  A = 'A',
}
